 .bar:before {
	 top: -8px;
}
 .bar:after {
	 top: 8px;
}
 .b-a-expanded, .menu-expanded .bar:before, .menu-expanded .bar:after {
	 transition: all 0.25s;
	 top: 0px;
}
 .menu-expanded {
     position: fixed;
     z-index: 9999999999999;
	 transition: all 0.25s;
     margin-top: 10vh;
	 text-align: center;
	 line-height: 200px;
	 height: 100%;
	 width: 100%;
	 border-radius: 0px;
	 top: 0;
	 left: 0;
	 background-color: #24292A;
}
 .menu-expanded ul {
	 transition: all 0s;
     text-decoration: none;

	 position: relative;
	 left: 0;
	 z-index: 2;
}
 .menu-expanded a {
	 transition: all 0.15s;
	 text-decoration: none;
     width: 90vw;
     height: 25vh;
	 font-size: 16px;
	 padding: 5px;
	 color: #fff;
	 display: block;
}
 .menu-expanded a:hover {
	
	 transition: all 0.15s;
	 letter-spacing: 2px;
	 
}
 .menu-expanded .bar {
	 background-color: transparent;
	 transition: all 0.25s;
}
 .menu-expanded .bar:before {
	 transform: rotate(45deg);
}
 .menu-expanded .bar:after {
	 transform: rotate(-45deg);
}
}

